.ax-hidden {
  visibility: hidden;
  position: absolute;
}

.carousel {
  --carousel-height: 400px;
  --carousel-width: 1000px;
  --carousel-item-height: 150px;
  --carousel-item-width: 150px;
  width: 100%;
}

.carousel-container {
  min-height: var(--carousel-height);
  max-width: var(--carousel-width);
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.carousel-item {
  height: var(--carousel-item-height);
  opacity: 0;
  width: var(--carousel-item-width);
  z-index: 0;
  transition: all .3s ease-in-out;
  position: absolute;
  transform: translateX(-50%);
}

.carousel-item-1 {
  opacity: .4;
  left: 15%;
}

.carousel-item-2, .carousel-item-4 {
  height: calc(var(--carousel-item-height) * 1.5);
  opacity: 1;
  width: calc(var(--carousel-item-width) * 1.5);
  z-index: 1;
}

.carousel-item-2 {
  left: 30%;
}

.carousel-item-3 {
  height: calc(var(--carousel-item-height) * 2);
  opacity: 1;
  width: calc(var(--carousel-item-width) * 2);
  z-index: 2;
  left: 50%;
  box-shadow: 0 0 30px #fff9, 0 0 60px #ffffff73, 0 0 110px #ffffff40, 0 0 100px #ffffff1a;
}

.carousel-item-4 {
  left: 70%;
}

.carousel-item-5 {
  opacity: .4;
  left: 85%;
}

.carousel-controls {
  justify-content: center;
  margin: 30px 0;
  display: flex;
}

.carousel-control {
  color: #aaa;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid;
  border-radius: 4px;
  width: 22px;
  height: 22px;
  margin: 0 20px;
  transition: transform .5s ease-out;
  position: relative;
  transform: scale(1.5);
}

.carousel-control:hover {
  transform: scale(1.3);
}

.carousel-control-previous:after, .carousel-control-previous:before {
  box-sizing: border-box;
  content: "";
  height: 8px;
  display: block;
  position: absolute;
  top: 5px;
}

.carousel-control-previous:before {
  background: currentColor;
  border-radius: 2px;
  width: 2px;
  right: 11px;
}

.carousel-control-previous:after {
  border-top: 4px solid #0000;
  border-bottom: 4px solid #0000;
  border-right: 5px solid;
  width: 0;
  right: 5px;
}

.carousel-control-next:after, .carousel-control-next:before {
